import cuDialog from "../../colorui/components/cu-dialog.vue"
import {
	mapState
} from 'vuex'

export default {
	components:{
		cuDialog
	},
	
	data() {
		return {
			showModal: false,
			modalDetail: {},
			confirmAction: function() {
					uni.redirectTo({
						url: "/pages/profile/profile"
					})
				}
		}
	},
	computed: mapState(['hasLogin']),
	methods:{
		onCreateGroup() {
			console.log('HasLogin: %s', this.hasLogin)
			if ( !this.hasLogin ) {
				this.showModal = true
			}
			else {
				uni.navigateTo({
					url: "/pages/group/group-create"
				})
			}
		},
		exploreGroup() {
			uni.redirectTo({
				url: "/pages/tabBar/explore/explore"
			})
		},
		viewProfile() {
			uni.redirectTo({
				url: "/pages/profile/profile"
			})
		},
		viewHome() {
			uni.redirectTo({
				url: "/pages/tabBar/home/home"
			})
		},
		hideModal(e) {
			this.showModal = false
		},
		doAction() {
			uni.redirectTo({
				url: "/pages/profile/profile"
			})
		}
	}
}
